













#index_box {
  position: relative;
  margin: auto;
  text-align: center;
  border: solid #ccc 1px;
  background-color: #fff;
  z-index: 1000;
}
@media screen and (max-width: 8000px) {
#index_box {
      width: 50%;
      top: 285px;
      padding: 50px;
}
}
@media screen and (max-width: 680px) {
#index_box {
      top: 285px;
}
}
@media screen and (max-width: 480px) {
#index_box {
      width: 80%;
      top: 285px;
      padding: 10px;
}
}
#index_box .index_h1 {
    text-decoration: none;
    color: #707070;
}
@media screen and (max-width: 8000px) {
#index_box .index_h1 {
        font-size: 98px;
}
}
@media screen and (max-width: 680px) {
#index_box .index_h1 {
        font-size: 49px;
}
}
@media screen and (max-width: 480px) {
#index_box .index_h1 {
        font-size: 24px;
}
}
#index_box #index_ul {
    margin-top: 50px;
    padding: 0;
}
#index_box .index_li {
    display: inline;
    margin: 20px;
}
#index_box .index_a {
    padding: 5px 20px;
    text-decoration: none;
    border-radius: 5px;
    border: solid #000 0px;
    -webkit-transition: all 0.5s ease-in-out;
    transition: all 0.5s ease-in-out;
    color: #707070;
}
#index_box .index_a:hover {
      color: #eee;
      background-color: #666;
}

.floatword_box {
  margin: 0;
  padding: 0;
  opacity: 0.5;
}
@media screen and (max-width: 8000px) {
.floatword_box {
      font-size: 36px;
}
}
@media screen and (max-width: 680px) {
.floatword_box {
      font-size: 28px;
}
}
@media screen and (max-width: 480px) {
.floatword_box {
      font-size: 24px;
}
}
